import { Stack, Link } from "@mui/material"
import { Link as RouterLink } from "react-router-dom"
import { SFooterGrid } from "./index.style"

const AppFooter = () => {
	const extLinks = [
		{
			name: "FAQs",
			link: "/faqs"
		},
		{
			name: "Privacy",
			link: "/privacy"
		},
		{
			name: "Terms",
			link: "/terms"
		}
	]

	return (
		<SFooterGrid item>
			<Stack direction="row" spacing={4}>
				{extLinks.map(({ name, link }) => (
					<Link
						component={RouterLink}
						color="primary"
						underline="none"
						variant="subtitle2"
						to={link}
						fontWeight="bold"
						key={link}
					>
						{name}
					</Link>
				))}
			</Stack>
		</SFooterGrid>
	)
}

export default AppFooter
