import {
	Card,
	CardActions,
	CardContent,
	CardHeader,
	Stack,
	Link,
	Box,
	LinearProgress
} from "@mui/material"
import { FC } from "react"
import { IAuthContainer } from "./AuthContainer.types"
import { ReactComponent as EZLOGO } from "../../assets/svgs/EZLOGO.svg"
import { Link as RouterLink } from "react-router-dom"
import BGImage from "../../assets/svgs/BG.svg"

const BoxStyle = {
	display: "grid",
	placeItems: "center",
	height: "100vh",
	backgroundImage: `url(${BGImage})`,
	backgroundRepeat: "no-repeat",
	backgroundSize: "cover"
}

const extLinks = [
	{
		name: "FAQs",
		link: "/faqs"
	},
	{
		name: "Privacy",
		link: "/privacy"
	},
	{
		name: "Terms",
		link: "/terms"
	}
]

const AuthContainer: FC<IAuthContainer> = ({ loading = false, children }) => {
	return (
		<Box sx={BoxStyle}>
			<Card
				sx={{
					width: `clamp(300px,70%,430px)`,
					// minWidth: 440,
					minHeight: 438,
					// height: "100%",
					// maxHeight: 800,
					display: "flex",
					flexDirection: "column",
					borderRadius: 2.5
				}}
				elevation={11}
				variant="elevation"
				raised
			>
				{loading && <LinearProgress />}
				<CardHeader sx={{ mx: 2 }} avatar={<EZLOGO />} />
				<CardContent
					sx={{
						// display: "flex",
						// flexDirection: "column",
						flex: "1 0 0",
						// overflow: "auto",
						mx: 2
					}}
				>
					{children}
				</CardContent>
				<CardActions sx={{ backgroundColor: "#f4f4f4", height: "80px" }}>
					<Stack direction="row" spacing={4} sx={{ margin: "auto" }}>
						{extLinks.map(({ name, link }) => (
							<Link
								component={RouterLink}
								color="inherit"
								underline="none"
								variant="subtitle2"
								to={link}
								fontWeight="bold"
								key={link}
							>
								{name}
							</Link>
						))}
					</Stack>
				</CardActions>
			</Card>
		</Box>
	)
}

export default AuthContainer
