import { BorderBottom } from "@mui/icons-material"
import {
	AppBar,
	Avatar,
	Box,
	Breadcrumbs,
	ClickAwayListener,
	Container,
	Divider,
	Grid,
	IconButton,
	Link as MuiLink,
	Menu,
	MenuItem,
	Paper,
	Popper,
	Toolbar,
	Tooltip,
	Typography
} from "@mui/material"
import React, { MouseEvent } from "react"
import { ReactComponent as EZLOGO } from "../../assets/svgs/EZLOGO.svg"
import AppsIcon from "@mui/icons-material/Apps"
import {
	SAppIcon,
	SBox,
	SHeaderAppBar,
	SHeaderToolbar,
	SMenu,
	SPaper
} from "./index.style"
import { ExpandMoreIcon } from "../../utils/Icons"
import { stringAvatar } from "../../utils/AvatarUtill"
import getBreadCrumbs from "../../utils/getBreadCrumbs"
import { useLocation, Link, useNavigate } from "react-router-dom"
import { useAuthContext } from "../../context/AuthContext/AuthContext"
import { useQuery, useQueryClient, UseQueryResult } from "react-query"
import { AxiosError, AxiosResponse } from "axios"
import { FullWidthBoxStyled } from "../ScrollableStack/index.styles"
import { IGetServiceProvidersData } from "../../components/ProductContainer/ProductContainer"
import { get } from "../../api/utils"
import ImageCard from "../../components/ImageCard"

const pages = ["Products", "Pricing", "Blog"]
const settings = ["Profile", "Account", "User Management", "Logout"]
// interface IProfileData {
// 	address: string
// 	contact_no: string
// 	email: string
// 	first_name: string
// 	last_name: string
// 	profile_image: string
// }

// interface IProfileResponse {
// 	user_info: IProfileData
// }

function getServiceProviders() {
	return get("service-providers")
}

const AppNavbar = () => {
	const [anchorElNav, setAnchorElNav] = React.useState(null)
	const [anchorElUser, setAnchorElUser] = React.useState(null)
	const [anchorElApps, setAnchorElApps] = React.useState<null | HTMLElement>(
		null
	)
	const location = useLocation()
	const navigate = useNavigate()
	const auth = useAuthContext()

	const {
		data: result,
		isLoading,
		isError,
		error
	}: UseQueryResult<
		AxiosResponse<IGetServiceProvidersData>,
		AxiosError
	> = useQuery("splist", getServiceProviders, { refetchOnWindowFocus: false })
	
	const serviceproviders = result?.data?.serviceproviders
	const handleOpenNavMenu = (event: any) => {
		setAnchorElNav(event.currentTarget)
	}
	const handleOpenUserMenu = (event: any) => {
		setAnchorElUser(event.currentTarget)
	}

	const handleCloseNavMenu = () => {
		setAnchorElNav(null)
	}

	const handleCloseUserMenu = () => {
		setAnchorElUser(null)
	}

	const handleClick = (e: MouseEvent<HTMLElement>) => {
		setAnchorElApps(anchorElApps ? null : e.currentTarget)
	}

	const handleClickAway = () => {
		setAnchorElApps(null)
	}

	const open = Boolean(anchorElApps)
	const id = open ? "apps-menu" : undefined
	const handleUserMenuClick = async (setting: string) => {
		switch (setting) {
			case "Profile": {
				navigate("/profile")
				break
			}

			case "Account": {
				break
			}
			case "User Management": {
				navigate("/user-management")
				break
			}
			case "Logout": {
				if (auth) {
					const result = await auth.logOut()
				}
				break
			}
		}
	}
	return (
		<Grid item>
			<SHeaderAppBar position="static" color="inherit" elevation={0}>
				<SHeaderToolbar disableGutters>
					<EZLOGO width={180} height={42} />
					<SBox display="flex">
						<ClickAwayListener
							mouseEvent="onMouseDown"
							touchEvent="onTouchStart"
							onClickAway={handleClickAway}
						>
							<Box>
								<IconButton aria-describedby={id} onClick={handleClick}>
									<AppsIcon fontSize="large" sx={{ color: "#454545" }} />
								</IconButton>
								<Popper
									id={id}
									open={open}
									onClick={handleClickAway}
									anchorEl={anchorElApps}
								>
									<SPaper elevation={2}>
										{serviceproviders?.map(
											({ id, name, is_accessible, image, host_url }) => (
												<a
													key={name}
													target="_blank"
													href={`http://${host_url}`}
												>
													<SAppIcon key={id}>
														<img
															style={{ maxWidth: "40%", flex: 1 }}
															src={`${process.env.REACT_APP_SERVER_URL}/${image}`}
															alt="logos"
														/>

														<Typography
															sx={{
																maxWidth: "100%",
																overflow: "hidden",
																whiteSpace: "nowrap",
																textOverflow: "ellipsis",
																textAlign: "center",
																color: "gray"
															}}
															color="inherit"
															variant="caption"
														>
															{name}
														</Typography>
													</SAppIcon>
												</a>
											)
										)}
									</SPaper>
								</Popper>
							</Box>
						</ClickAwayListener>
						<Avatar
							{...stringAvatar("Fahad Sohail")}
							alt="Remy Sharp"
							src="/static/images/avatar/2.jpg"
						/>

						<Tooltip title="Open settings">
							<IconButton
								disableRipple
								onClick={handleOpenUserMenu}
								sx={{ p: 0 }}
							>
								<ExpandMoreIcon />
							</IconButton>
						</Tooltip>
						<SMenu
							sx={{ mt: "45px" }}
							id="menu-appbar"
							anchorEl={anchorElUser}
							anchorOrigin={{
								vertical: "top",
								horizontal: "right"
							}}
							keepMounted
							transformOrigin={{
								vertical: "top",
								horizontal: "right"
							}}
							open={Boolean(anchorElUser)}
							onClick={handleCloseUserMenu}
							onClose={handleCloseUserMenu}
						>
							{settings.map(setting => (
								<MenuItem
									divider
									key={setting}
									onClick={() => handleUserMenuClick(setting)}
								>
									<Typography textAlign="center">{setting}</Typography>
								</MenuItem>
							))}
						</SMenu>
					</SBox>
				</SHeaderToolbar>
			</SHeaderAppBar>
			<Container maxWidth="xl">
				<Box my={1} pl={2.5}>
					<Breadcrumbs aria-label="breadcrumb">
						{getBreadCrumbs(location).map((crumb, idx) => {
							if (crumb.link) {
								return (
									<MuiLink
										key={idx}
										component={Link}
										to={crumb.link}
										underline="hover"
										color="primary"
									>
										{crumb.label}
									</MuiLink>
								)
							} else {
								return (
									<Typography key={idx} color="text.primary">
										{crumb.label}
									</Typography>
								)
							}
						})}
					</Breadcrumbs>
				</Box>
			</Container>
		</Grid>
	)
}

export default AppNavbar
