import { Box } from '@mui/material'
import IconContainer from '../IconContainer'
import Select from "../../components/FilterInput/FilterSelect"
import { IAppData } from '../../context/UserContext/UserContext.types'
import PracticesDropdown from '../PracticeDropdown/PracticesDropdown'
import { useUserContext } from '../../context/UserContext/UserContext'
import ErrorInfo from '../ErrorInfo'

type Props = {
  index: number
  Logo: /* React.ReactNode */string
  selected: boolean | undefined
  app: IAppData
  errors: Record<string, any> | undefined
  disabled: boolean
}

const ProductList = (props: Props) => {
  const { index, Logo, selected, app, errors, disabled } = props
  const appData = useUserContext()

  const setApp = (data: IAppData, appIndex: number) => {
    if(appData?.appData) {
      appData?.setAppData([
        ...appData?.appData?.map((t_app, idx) => {
          if (idx == appIndex) {
            return data;
          } else {
            return t_app;
          }
        })
      ]);
    }
  };

  const selectedRole = app.roles.find(role => role.isSelected)
  
  return (
    <Box display="flex" width="100%" gap={2} p={1} sx={({ palette }) => ({ backgroundColor: errors ? palette.error['A100'] : palette.secondary[200] })}>
      <Box onClick={() => {
        appData?.setSelected(app.id)
      }}
      sx={{ width: "20%", height: '100%', pointerEvents: appData?.disableAll ? 'none' : 'auto'}}
    >
        <IconContainer isActive={selected} disabled={appData?.disableAll}>
          <Box sx={{maxWidth:"100%", maxHeight: "100%", height: "100%"}}>
            <img src={`${process.env.REACT_APP_SERVER_URL}/${Logo}`} style={{ width: '100%', height: '100%' }} draggable={false}/>
          </Box>
        </IconContainer>
      </Box>
      <Box sx={{ display: 'flex', flexDirection: 'column', width: "80%" }}>
        <Box pb={1} sx={{ display: 'flex', gap: 2, width: "100%" }}>
          <Box sx={{ width: "65%" }}>
            { app.name !== 'ez-login' && <PracticesDropdown app={app} appIndex={index} setApp={setApp} disabled={disabled} /> }
          </Box>
          <Box sx={{ width: "35%" }}>
            <Select
              value={selectedRole?.id?.toString() || ""}
              placeholder="Role"
              onChange={(value) => {
                appData?.setSubrole(app.id, null)
                if(value) {
                  appData?.setRole(app.id, parseInt(value))
                } else {
                  appData?.setRole(app.id, null)
                }
              }}
              disabled={disabled}
            >
              {
                app.roles.map(role => {
                  return (
                    <option key={role.id} label={role.name} value={role.id}>
                      {role.name}
                    </option>
                  )
                })
              }
            </Select>
          </Box>
          <Box width={20}>
            { errors && <ErrorInfo messages={[
              ...(Array.isArray(errors) ? errors : []),
              typeof errors === 'string' && errors,
              ...errors?.role?.id || [],
              ...errors?.role?.sub_role || [],
              ...errors?.practices || [],
              ...errors?.dr_iq_gender_id || []
              ]}
              /> }
          </Box>
        </Box>
        {app.name == 'dr-iq' &&
          <Box sx={{ display: 'flex', gap: 2, width: "100%" }}>
            <Box sx={{ width: "50%" }}>
              <Select
                disabled={disabled}
                value={app.gender.find(g => g.isSelected)?.id?.toString() || ""}
                placeholder="Gender"
                onChange={(value) => {
                  if(value) {
                    appData?.setGender(app.id, parseInt(value))
                  } else {
                    appData?.setGender(app.id, null)
                  }
                }}
              >
                {
                  app.gender.map(gen => {
                    return (
                      <option key={gen.id} label={gen.name} value={gen.id}>
                        {gen.name}
                      </option>
                    )
                  })
                }
              </Select>
            </Box>
            <Box sx={{ width: "50%" }}>
              <Select
                disabled={!selectedRole?.id || disabled}
                value={selectedRole?.sub_roles?.find(ele => ele.isSelected)?.id?.toString() || ""}
                placeholder="Sub Role"
                onChange={(value) => {
                  if(value) {
                    appData?.setSubrole(app.id, parseInt(value))
                  } else {
                    appData?.setSubrole(app.id, null)
                  }
                }}
              >
                {
                  selectedRole ? selectedRole?.sub_roles?.map(role => {
                    return (
                      <option key={role.id} label={role.name} value={role.id}>
                        {role.name}
                      </option>
                    )
                  }) : null
                }
              </Select>
            </Box>
            <Box width={20}></Box>
          </Box>
        }
      </Box>
    </Box>
  )
}

export default ProductList