import { Card, styled } from "@mui/material"
import { ISCard } from "./ImageCard.types"

export const SCard = styled(Card, {
	//to control the prop injected to dom element
	shouldForwardProp: prop => prop !== "disabled"
})<ISCard>(({ theme: { palette }, disabled }) => ({
	display: "flex",
	alignItems: "center",
	justifyContent: "center",
	borderRadius:"7px",
	width: "93px",
	height: "90px",
	position: "relative",
	boxShadow: `4px 4px 12px 0 rgba(23, 50, 104, 0.13)`,
	pointerEvents: disabled ? "none" : "initial"
	// opacity: disabled ? alpha(palette.secondary["900"]||"#000", 1) : 1
}))
