import {
	AppBar,
	Box,
	Grid,
	Menu,
	Toolbar,
	menuClasses,
	Paper
} from "@mui/material"
import { styled } from "@mui/system"

export const SFooterGrid = styled(Grid)(({ theme: { palette, spacing } }) => ({
	borderTop: `1px solid ${palette.secondary["A200"]}`,
	padding: spacing(2.5, 3, 1.25)
}))

export const SHeaderAppBar = styled(AppBar)(
	({ theme: { palette, spacing } }) => ({
		borderBottom: `1px solid ${palette.secondary["A200"]}`,
		padding: spacing(0, 4)
	})
)
export const SHeaderToolbar = styled(Toolbar)(() => ({
	display: "flex",
	justifyContent: "space-between",
	alignItems: "center"
}))
export const SBox = styled(Box)(({ theme: { spacing } }) => ({
	alignItems: "center",
	gap: spacing(1.5)
}))
export const SMenu = styled(Menu)(({ theme: { spacing } }) => ({
	// [`& .${menuClasses.root}`]: {
	// 	minWidth: "200px"
	// },
	"& .MuiPaper-root": {
		minWidth: "200px"
	}
}))

export const SPaper = styled(Paper)(({ theme: { spacing } }) => ({
	width: 250,
	height: 270,
	padding: spacing(2),
	display: "grid",
	gridTemplateColumns: "repeat(3,1fr)",
	gridAutoRows: "80px",
	alignItems: "center",
	justifyContent: "center"
}))

export const SAppIcon = styled(Box)(({ theme: { spacing } }) => ({
	width: "70px",
	minHeight: "70px",
	display: "flex",
	flexDirection: "column",
	alignItems: "center",
	justifyContent: "center",
	borderRadius: "5px",
	cursor: "pointer",
	"&:hover": {
		backgroundColor: "lightgrey",
		"& span": {
			whiteSpace: "normal",
			wordWrap: "break-word"
		}
	}
}))
