import {
	Avatar,
	Badge,
	Box,
	Link,
	IconButton,
	Typography,
	Skeleton,
	CircularProgress,
	CircularProgressProps
} from "@mui/material"
import {
	AddCircleIcon,
	LockIcon,
	PersonIcon,
	SettingsIcon
} from "../../utils/Icons"
import {
	SListConatiner,
	SProfileImage,
	SProfileImageContainer,
	SProfileSideBar
} from "./index.styles"
import { NavLink as RouterLink } from "react-router-dom"
import { IImage, IImageUploadResponse, ISideBar } from "./Profile.types"
import { AxiosError } from "axios"
import { put } from "../../api/utils"
import { useMutation, UseMutationResult, useQueryClient } from "react-query"

const menuList = [
	{
		icon: <SettingsIcon sx={{ color: "#c8c8c8", fontSize: "1rem" }} />,
		label: "Account Settings",
		to: "/profile",
		isActive: true
	},
	{
		icon: <PersonIcon sx={{ color: "#c8c8c8", fontSize: "1rem" }} />,
		label: "Personal Information",
		to: "/profile",
		isActive: false
	},
	{
		icon: <LockIcon sx={{ color: "#c8c8c8", fontSize: "1rem" }} />,
		label: "Privacy",
		to: "/profile",
		isActive: false
	}
]

let activeStyle = {
	textDecoration: "underline"
}

function imageUpload(imageData: IImage) {
	const formData = new FormData()
	formData.append("image", imageData.image as Blob)
	return put<IImageUploadResponse>("user/profile-image", formData)
}

const Sidebar = ({ profileImage, fullName, isLoading }: ISideBar) => {
	const queryClient = useQueryClient()

	//mutation to update the Image
	const {
		data: ImageData,
		mutate,
		isLoading: isProfileLoading
	}: UseMutationResult<IImageUploadResponse, AxiosError, IImage> = useMutation<
		IImageUploadResponse,
		AxiosError,
		IImage
	>(imageUpload, {
		onSuccess: () => {
			console.log("Image Uploaded Successfully")
			queryClient.invalidateQueries("profile")
		}
	})

	const handleFileUpload: React.ChangeEventHandler<
		HTMLInputElement
	> = async e => {
		if (e?.target) {
			const image = e.target.files?.[0]
			if (image) {
				mutate({ image })
			}
		}
	}
	return (
		<SProfileSideBar>
			<SProfileImageContainer>
				<SProfileImage>
					{isLoading && (
						<Box>
							<Skeleton
								animation="wave"
								variant="circular"
								width={100}
								height={100}
							/>
						</Box>
					)}
					{!isLoading && (
						<Badge
							overlap="circular"
							anchorOrigin={{ horizontal: "right", vertical: "top" }}
							badgeContent={
								<IconButton size="small" color="primary">
									<label htmlFor="upload-photo-fahad">
										<input
											id="upload-photo-fahad"
											accept="image/*"
											type="file"
											onChange={handleFileUpload}
											style={{ display: "none" }}
										/>
										<AddCircleIcon width={28} height={28} />
									</label>
								</IconButton>
							}
						>
							<CircularProgressWithChildren isLoading={isProfileLoading}>
								<Avatar
									sx={{ width: 100, height: 100 }}
									src={
										`${process.env.REACT_APP_SERVER_URL}/${profileImage}` ||
										"https://picsum.photos/200"
									}
								/>
							</CircularProgressWithChildren>
						</Badge>
					)}
				</SProfileImage>
				<Typography color="primary" variant="subtitle1" fontWeight="bold">
					{isLoading ? <Skeleton animation="wave" width={100} /> : fullName}
				</Typography>
			</SProfileImageContainer>
			<SListConatiner>
				{menuList.map(({ icon, label, to, isActive }, id) => (
					<Box
						key={id}
						sx={{
							display: "flex",
							alignItems: "center",
							gap: "1rem"
						}}
					>
						{icon}
						<Box>
							<Link
								disabled={!isActive}
								component="button"
								// component={RouterLink}
								color="#636363"
								underline="none"
								variant="body1"
								// to={to}
								fontWeight="600"
								key={label}
								// style={({ isActive }) => console.log(isActive)}
							>
								{label}
							</Link>
						</Box>
					</Box>
				))}
			</SListConatiner>
		</SProfileSideBar>
	)
}

export default Sidebar

function CircularProgressWithChildren({
	isLoading,
	...rest
}: CircularProgressProps & {
	isLoading: boolean
	children: React.ReactNode
}) {
	return (
		<Box sx={{ position: "relative", display: "flex", alignItems: "center" }}>
			<CircularProgress
				thickness={1}
				size={110}
				variant="indeterminate"
				style={{ visibility: isLoading ? "visible" : "hidden" }}
				{...rest}
			/>

			<Box
				sx={{
					top: 0,
					left: 5,
					bottom: 0,
					right: 0,
					position: "absolute",
					display: "flex",
					alignItems: "center"
				}}
			>
				{rest.children}
			</Box>
		</Box>
	)
}
