import { Box, OutlinedInputProps } from "@mui/material";
import InputLabel from './InputLabel'
import InputBase from './InputBase'

export interface IFilterInputProps<T> {
  label: string | null,
  value: T,
  onChange: OutlinedInputProps["onChange"],
  endAdornment?: OutlinedInputProps["endAdornment"]
  placeholder?: string
}

function FilterInput<T>(props: IFilterInputProps<T>) {
  const { label, endAdornment, ...restProps } = props

  return (
    <Box>
      { label && <InputLabel>{label}</InputLabel> }
      <InputBase {...restProps} endAdornment={endAdornment}/>
    </Box>
  )
}

export default FilterInput