import { Box, Typography, Divider, Stack, Skeleton } from "@mui/material"
import { useQuery, UseQueryResult } from "react-query"
import { FullWidthBoxStyled } from "../../layout/ScrollableStack/index.styles"
import ImageCard from "../../components/ImageCard"

import { IProductContainer } from "./ProductContainer.types"
import { get } from "../../api/utils"
import { AxiosError, AxiosResponse } from "axios"

function getServiceProviders() {
	return get("service-providers")
}

interface IServiceProvider {
	host_url: string
	id: number
	image: string
	is_accessible: boolean
	name: string
}

export interface IGetServiceProvidersData {
	message: "success"
	serviceproviders: IServiceProvider[]
	statuscode: number
}

const ProductContainer = <
	T extends { name: string; icon: string; disabled?: boolean }
>({
	products
}: IProductContainer<T>) => {
	const {
		data: result,
		isLoading,
		isError,
		error
	}: UseQueryResult<
		AxiosResponse<IGetServiceProvidersData>,
		AxiosError
	> = useQuery("splist", getServiceProviders)

	const serviceproviders = result?.data?.serviceproviders

	if (isLoading)
		return (
			<Stack
				spacing={3}
				direction="row"
				flexWrap="wrap"
				justifyContent="center"
				gap={1}
			>
				{Array(7)
					.fill(true)
					.map((val, idx) => (
						<Stack key={idx} spacing={1}>
							<Skeleton variant="rectangular" width={93} height={90} />
							<Skeleton variant="text" width={93} />
						</Stack>
					))}
			</Stack>
		)

	if (isError) return <>Error: {error.message}</>
	if (!serviceproviders?.length)
		return (
			<Typography textAlign="center" variant="body1">
				No Product available
			</Typography>
		)
	return (
		<FullWidthBoxStyled justifyContent="flex-start" flexWrap="wrap" gap={3}>
			{serviceproviders.map(({ id, name, is_accessible, image }) => (
				<Box
					key={id}
					display="flex"
					flexDirection="column"
					alignItems="center"
					gap={2}
				>
					<ImageCard icon={image} disabled={!is_accessible} />
					<Typography color="primary.900" fontWeight="bold" variant="body1">
						{name}
					</Typography>
				</Box>
			))}
		</FullWidthBoxStyled>
	)
}

export default ProductContainer
