import {
	Box,
	Button,
	Card,
	CardHeader,
	Container,
	Divider,
	LinearProgress,
	Skeleton,
	Stack,
	Switch,
	TextField,
	Typography
} from "@mui/material"
import { SBox, SCardActions, SCardContent } from "./index.styles"
import ProductList from "../../components/ProductList/ProductList"
import { IAddUser } from "./AddUser.types"
import { useUserContext } from "../../context/UserContext/UserContext"
import ErrorInfo from "../../components/ErrorInfo"
import { useNavigate } from "react-router-dom"
import { DataLoadError } from "../../components/ErrorScreens"

const AddUser = (props: IAddUser) => {
	const { editMode } = props
	const navigate = useNavigate()

	const appData = useUserContext()

	const productList = appData?.appDataLoading ? (
		<Box width="100%" display="flex" flexDirection="column">
			{new Array(6).fill(0).map((_ele, idx) => (
				<Skeleton key={idx} height={100} sx={{ margin: -2 }} />
			))}
		</Box>
	) : (
		appData?.appData?.map((app: any, index: any) => {
			return (
				<ProductList
					key={app.id}
					app={app}
					index={index}
					Logo={app.sp_app_image}
					selected={app.selected}
					disabled={!app.selected || appData?.disableAll}
					errors={appData?.errors?.apps?.[index]}
				/>
			)
		})
	)

	return (
		<Container maxWidth="md" fixed>
			<Card sx={{ maxWidth: 781 }}>
				<CardHeader
					disableTypography
					title={
						<Typography variant="body1" color="primary" fontWeight={"bold"}>
							{editMode ? "Edit User" : "Add New User"}
						</Typography>
					}
				/>
				<Divider />
				{appData?.dataLoadError ? (
					<DataLoadError />
				) : (
					<>
						<SCardContent>
							<SBox
								{...(appData?.errors?.firstname && { bgcolor: "error.A100" })}
							>
								<Typography sx={{ flex: 1 }}>First Name</Typography>
								<TextField
									sx={{ flex: 4 }}
									fullWidth
									placeholder="First Name"
									value={appData?.userInfo?.firstname}
									onChange={e =>
										appData?.setUserInfo({
											...appData?.userInfo,
											firstname: e.target.value
										})
									}
									disabled={appData?.disableAll}
								/>
								{appData?.errors?.firstname && (
									<ErrorInfo messages={appData?.errors?.firstname} />
								)}
							</SBox>
							<SBox
								{...(appData?.errors?.lastname && { bgcolor: "error.A100" })}
							>
								<Typography sx={{ flex: 1 }}>Last Name</Typography>
								<TextField
									sx={{ flex: 4 }}
									fullWidth
									placeholder="Last Name"
									value={appData?.userInfo?.lastname}
									onChange={e =>
										appData?.setUserInfo({
											...appData?.userInfo,
											lastname: e.target.value
										})
									}
									disabled={appData?.disableAll}
								/>
								{appData?.errors?.lastname && (
									<ErrorInfo messages={appData?.errors?.lastname} />
								)}
							</SBox>
							<SBox {...(appData?.errors?.email && { bgcolor: "error.A100" })}>
								<Typography sx={{ flex: 1 }}>Email Address</Typography>
								<TextField
									sx={{ flex: 4 }}
									fullWidth
									placeholder="username@email.com"
									value={appData?.userInfo?.email}
									onChange={e =>
										appData?.setUserInfo({
											...appData?.userInfo,
											email: e.target.value
										})
									}
									disabled={appData?.disableAll || editMode}
								/>
								{appData?.errors?.email && (
									<ErrorInfo messages={appData?.errors?.email} />
								)}
							</SBox>
							{editMode && (
								<SBox
									{...(appData?.errors?.is_active && { bgcolor: "error.A100" })}
								>
									<Typography sx={{ flex: 1 }}>Status</Typography>
									<Stack
										direction="row"
										spacing={1}
										alignItems="center"
										flex={4}
									>
										<Typography variant="caption">Inactive</Typography>
										<Switch
											checked={appData?.userInfo?.is_active}
											onChange={(_e, checked) =>
												appData?.setUserInfo({
													...appData?.userInfo,
													is_active: checked
												})
											}
											inputProps={{ "aria-label": "user status" }}
											disabled={appData?.disableAll}
										/>
										<Typography variant="caption">Active</Typography>
									</Stack>
								</SBox>
							)}
							<Box display="flex">
								<Typography sx={{ flex: 1 }}>Products</Typography>
								<SBox display="flex" flexDirection="column" flex={4}>
									<Box display="flex" width="100%" gap={2}>
										<Box
											component={Typography}
											sx={{ width: "25%" }}
											color="text.mute"
										>
											Select Product
										</Box>
										<Box
											component={Typography}
											sx={{ width: "50%" }}
											color="text.mute"
										>
											Select Practice
										</Box>
										<Box
											component={Typography}
											sx={{ width: "30%" }}
											color="text.mute"
										>
											Select Role
										</Box>
									</Box>
									<Box
										width="100%"
										display="flex"
										flexDirection="column"
										mt={1}
										gap={0.8}
									>
										{productList}
									</Box>
								</SBox>
							</Box>
						</SCardContent>
						<Divider />
						<SCardActions>
							<Button
								variant="outlined"
								onClick={() => navigate("/user-management")}
							>
								Cancel
							</Button>
							{!appData?.disableAll && (
								<Button
									variant="contained"
									onClick={appData?.addUser}
									disabled={appData?.appDataLoading}
								>
									Save
								</Button>
							)}
							{appData?.disableAll && !appData.appDataLoading && (
								<Button variant="contained" onClick={appData?.addNew}>
									Add Another
								</Button>
							)}
						</SCardActions>
					</>
				)}
				{appData?.isCreatingUser && <LinearProgress />}
			</Card>
		</Container>
	)
}

export default AddUser
