import React, { useEffect, useState } from "react";
import { Box, Button, Popper, Typography, ClickAwayListener, ListItem, InputBase } from "@mui/material";
import Panel from "../ProductList/Panel";
import { IAppData, IPractice } from "../../context/UserContext/UserContext.types";
import { useUserContext } from "../../context/UserContext/UserContext";
import S_InputBox from "../FilterInput/SharpBox";
import { KeyboardArrowDown } from "@mui/icons-material";
import { styled } from "@mui/system";
import produce, { current } from "immer";
import { useDebouncedValue } from "../../hooks";

interface DDProps {
  // isLoading: boolean
  setApp: (app: IAppData, appIndex: number) => void
  appIndex: number
  app: IAppData
  disabled: boolean
}

const SearchBox = styled(InputBase)(({theme}) => ({
  padding: theme.spacing(1, 2)
}))

export default function PracticesDropdown(props: DDProps) {
  const { app, appIndex, setApp, disabled } = props;
  const [ search, setSearch ] = useState('')
  const deferredSearch = useDebouncedValue(search, 700)

  const [localState, setLocalState] = React.useState<IAppData>(app)
  const [filteredPractices, setFilteredPractices] = React.useState<IAppData>(app)
  const [anchorEl, setAnchorEl] = React.useState<HTMLElement | null>(null);
  const appData = useUserContext()

  useEffect(() => {
    setLocalState(app)
  }, [app])

  useEffect(() => {
    const filtered = filterPractices()
    setFilteredPractices(filtered)
  }, [localState])

  useEffect(() => {
    if(deferredSearch) {
      const filtered = filterPractices()
      setFilteredPractices(filtered)
    } else {
      setFilteredPractices(localState)
    }
  }, [deferredSearch])

  const handleClick = (event: React.SyntheticEvent) => {
    setAnchorEl(anchorEl ? null : event.currentTarget as HTMLElement);
  };

  const deselectPrev = (obj: IPractice | IAppData, status: boolean) => {
    if (!obj?.prev) {
      return;
    }

    if (obj?.checkedLength && obj?.practices && (obj?.checkedLength - 1 > 0 || status)) {
      obj.isIndeterminate = true;
    } else {
      obj.isIndeterminate = false;
    }
    obj.isChecked = false;
    deselectPrev(obj.prev, obj.isIndeterminate);
  };

  const selectPrev = (obj: IPractice | IAppData) => {
    if (!obj?.prev) {
      return;
    }

    if (
      (obj.hasOwnProperty('checkedLength') && isFinite(obj.checkedLength as number)) &&
      obj?.practices &&
      obj.checkedLength as number + 1 >= obj?.practices?.length
    ) {
      obj.isIndeterminate = false;
      obj.isChecked = true;
    } else {
      obj.isIndeterminate = true;
    }
    selectPrev(obj.prev);
  };

  const selectChildren = (obj: IPractice) => {
    if (!obj?.practices) {
      return;
    }
    obj.isIndeterminate = false;

    obj.practices.forEach((pract) => {
      if (obj.isChecked) {
        pract.isChecked = true;
        
      } else {
        pract.isChecked = false;
        pract.isIndeterminate = false;
      }
      selectChildren(pract);
    });
  };

  function deepClone(obj: IPractice): IPractice {
    if(!obj.practices) {
      return { ...obj }
    }
    const clonePractices = obj.practices.map((practice) => {
      return deepClone({...practice})
    })
    
    return { ...obj, practices: clonePractices }
  }

  const onCheck = (_option: IPractice, indexArray: number[]) => {

    const foundIndex = localState.practices.findIndex(ele => ele.id === indexArray[0])
    const copyPractice = deepClone(localState.practices[foundIndex])
    let tempLocalData = { ...localState }
    tempLocalData.practices = [ ...tempLocalData.practices ]
    tempLocalData.practices[foundIndex] = copyPractice

    let temp: IAppData | IPractice = tempLocalData;

    indexArray.forEach((ele, index) => {
      let tmp = temp;
      tmp.checkedLength = tmp?.practices?.filter(
        (practice) => practice.isChecked || practice.isIndeterminate
      )?.length || 0;

      temp = temp.practices!.find((itm) => itm.id === ele) as IPractice
      temp.prev = tmp;

      if (index === indexArray.length - 1) {
        if (!temp.isChecked) {
          temp.isChecked = true;
          selectPrev(temp);
        } else {
          temp.isChecked = false;
          deselectPrev(temp, false);
        }

        if (temp.practices?.length) {
          selectChildren(temp);
        }
      }
    });

    setLocalState(tempLocalData);
  };

  const deepSearch = (practice: IAppData | IPractice): boolean => {
    delete practice.prev
    if(!practice.practices || practice?.practices.length < 1) {
      return practice.name.includes(deferredSearch)
    } else {
      const copyPractices = current(practice.practices)
      const matched = practice.practices.filter((prac) => {
        return deepSearch(prac)
      })

      if(practice.name.includes(deferredSearch)) {
        practice.practices = copyPractices
      } else {
        practice.practices = matched
      }

      return !!matched.length || practice.name.includes(deferredSearch)
    }
  }

  function filterPractices() {
    if(deferredSearch) {
      const filtered: IAppData = produce(localState, draft => {
        deepSearch(draft)
      })

      return filtered

    } else {
      return localState
    }
  }

  const saveAndClose = () => {
    setApp(localState, appIndex)
    setAnchorEl(null)
  }

  const revokeAndClose = () => {
    setLocalState(app)
    setAnchorEl(null)
  }

  const id = "dropdown";
  const open = Boolean(anchorEl);

  const checkedLength = app.practices.filter((practice) => practice.isChecked || practice.isIndeterminate).length || 0

  if (appData?.appDataLoading) {
    return <Typography>Loading...</Typography>;
  }

  return (
    <>
      <S_InputBox {...(disabled ? {} : { onClick: handleClick })} sx={{ pointerEvents: disabled ? 'none' : 'auto' }}>
        <Box width='100%' display='flex' alignItems='center' justifyContent='space-between' p={2} pr={0.1}>
          <Typography variant='subtitle1' color={disabled ? 'text.disabled' : 'text.primary'}>{checkedLength ? `${checkedLength} Practices Selected` : `Practices`}</Typography>
          <KeyboardArrowDown color={disabled ? 'action' : 'inherit'} />
        </Box>
      </S_InputBox>
      {/* <Button aria-describedby={id} onClick={handleClick}>
        Practices
      </Button> */}
      <Popper id={id} open={open} anchorEl={anchorEl} sx={({ palette, shadows }) => ({
        backgroundColor: palette.background.default,
        boxShadow: shadows[2]
      })}>
      <ClickAwayListener onClickAway={saveAndClose} mouseEvent='onMouseDown'>
        <Box>
          <Box>
            <SearchBox
              value={search}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => setSearch(e.currentTarget.value)}
              placeholder="Search"
              autoFocus
              fullWidth
            />
            <Box
              maxHeight={400}
              width={anchorEl?.clientWidth}
              overflow="auto"
            >
              {/* <CheckTree valueKey='id' childrenKey='practices' labelKey='name' data={app.practices} onChange={values => console.log("CHECK: ", values)} /> */}
              {filteredPractices && filteredPractices?.practices?.length > 0 ? filteredPractices?.practices?.map((panel, index) => (
                <Panel
                  key={index}
                  index={[panel.id]}
                  option={panel}
                  onCheck={onCheck}
                  search={deferredSearch}
                  expanded={search ? !panel.name.includes(deferredSearch) : false}
                />
              )) : <ListItem><Typography variant="body1">No Options...</Typography></ListItem>}
            </Box>
          </Box>
          <Box sx={({palette, spacing}) => ({ 
              width: "100%",
              backgroundColor: palette.background.default,
              padding: spacing(1),
              borderTop: `1px solid ${palette.divider}`,
              display: 'flex',
              justifyContent: 'flex-end',
              '& :not(:last-child)': {
                marginRight: spacing(1)
              }
            })}>
              <Button
                variant='contained'
                color='secondary'
                size='small'
                onClick={revokeAndClose}
              >
                Cancel
              </Button>
              <Button
                variant='contained'
                color='primary'
                size='small'
                onClick={saveAndClose}
              >
                Save
              </Button>
            </Box>
          </Box>
      </ClickAwayListener>
      </Popper>
    </>
  );
}
