import {
	Box,
	InputAdornment,
	InputLabel,
	Skeleton,
	Typography
} from "@mui/material"
import FormInput from "../../components/FormInput/FormInput"
import StyledButton from "../../components/StyledButton"
import { EditIcon } from "../../utils/Icons"

const MainContent = ({
	mutationLoading,
	isLoading
}: {
	mutationLoading: boolean
	isLoading: boolean
}) => {
	return (
		<Box sx={{ flex: 1 }}>
			<Box
				sx={{
					padding: "2rem",
					pt: "3rem",
					display: "flex",
					flexDirection: "column",
					gap: "1rem"
				}}
			>
				<Typography variant="h5" fontWeight="bold" color="primary">
					{isLoading ? <Skeleton animation="wave" /> : "Account Setting"}
				</Typography>
				<Box
					sx={{
						display: "grid",
						gridTemplateColumns: `repeat(2,1fr)`,
						mt: "1rem",
						gap: "1rem"
					}}
				>
					<Box
						sx={{
							display: "flex",
							flexDirection: "column",
							gap: "0.75rem"
						}}
					>
						<InputLabel variant="standard">
							<Typography color="#444444">First Name</Typography>
						</InputLabel>
						{isLoading ? (
							<Skeleton animation="wave" height={60} />
						) : (
							<FormInput
								inputProps={{ style: { height: "1.6875rem" } }}
								fullWidth
								placeholder="Ryan"
								size="small"
								name="first_name"
								variant="outlined"
								type="text"
							/>
						)}
					</Box>
					<Box
						sx={{
							display: "flex",
							flexDirection: "column",
							gap: "0.75rem"
						}}
					>
						<InputLabel variant="standard">
							<Typography color="#444444">Last Name</Typography>
						</InputLabel>

						{isLoading ? (
							<Skeleton animation="wave" height={60} />
						) : (
							<FormInput
								inputProps={{ style: { height: "1.6875rem" } }}
								fullWidth
								placeholder="Gosling"
								size="small"
								name="last_name"
								variant="outlined"
							/>
						)}
					</Box>
					<Box
						sx={{
							display: "flex",
							flexDirection: "column",
							gap: "0.75rem"
						}}
					>
						<InputLabel variant="standard">
							<Typography color="#444444">Email</Typography>
						</InputLabel>

						{isLoading ? (
							<Skeleton animation="wave" height={60} />
						) : (
							<FormInput
								disabled
								aria-disabled
								inputProps={{ style: { height: "1.6875rem" } }}
								fullWidth
								placeholder="example@gmail.com"
								size="small"
								name="email"
								variant="outlined"
								InputProps={{
									endAdornment: (
										<InputAdornment position="start">
											<EditIcon fontSize="small" />
										</InputAdornment>
									)
								}}
							/>
						)}
					</Box>
					<Box
						sx={{
							display: "flex",
							flexDirection: "column",
							gap: "0.75rem"
						}}
					>
						{/* <InputLabel variant="standard">
                    <Typography color="#444444">Password</Typography>
                </InputLabel>
                <FormInput
                    inputProps={{ style: { height: "1.6875rem" } }}
                    fullWidth
                    placeholder="password"
                    size="small"
                    name="password"
                    type="password"
                    variant="outlined"
                    InputProps={{
                        endAdornment: (
                            <InputAdornment position="start">
                                <EditIcon fontSize="small" />
                            </InputAdornment>
                        )
                    }}
                /> */}
					</Box>
					<Box
						sx={{
							display: "flex",
							flexDirection: "column",
							gap: "0.75rem"
						}}
					>
						<InputLabel variant="standard">
							<Typography color="#444444">Phone number</Typography>
						</InputLabel>

						{isLoading ? (
							<Skeleton animation="wave" height={60} />
						) : (
							<FormInput
								inputProps={{ style: { height: "1.6875rem" } }}
								fullWidth
								placeholder="xxxx xxxx xxxx"
								size="small"
								name="contact_no"
								type="tel"
								variant="outlined"
								InputProps={{
									endAdornment: (
										<InputAdornment position="start">
											<EditIcon fontSize="small" />
										</InputAdornment>
									)
								}}
							/>
						)}
					</Box>
					<Box></Box>
					<Box
						sx={{
							display: "flex",
							flexDirection: "column",
							gap: "0.75rem",
							gridColumn: "1/-1"
						}}
					>
						<InputLabel variant="standard">
							<Typography color="#444444">Address</Typography>
						</InputLabel>
						{isLoading ? (
							<Skeleton animation="wave" height={60} />
						) : (
							<FormInput
								inputProps={{ style: { height: "1.6875rem" } }}
								fullWidth
								placeholder="Address"
								size="small"
								name="address"
								variant="outlined"
							/>
						)}
					</Box>
				</Box>
				<Box marginLeft="auto" mt="2rem" display="flex" gap={"1rem"}>
					<StyledButton variant="outlined">Cancel</StyledButton>
					<StyledButton
						disabled={mutationLoading}
						type="submit"
						variant="contained"
					>
						Save
					</StyledButton>
				</Box>
			</Box>
		</Box>
	)
}

export default MainContent
