import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import TableRow from '@mui/material/TableRow';
import TableBody from '@mui/material/TableBody';
import { styled } from '@mui/material/styles';
import { Box, Table, Pagination, PaginationItem } from '@mui/material';
import { SyncAlt } from '@mui/icons-material';

const StyledTable = styled(Table)(({ theme: { spacing } }) => ({
  borderCollapse: 'separate',
  borderSpacing: `0 ${spacing(0.3)}`,
}))

const StyledPaper = styled(Box)(() => ({
}))

const StyledTableBody = styled(TableBody)(() => ({
}))

const StyledTableCell = styled(TableCell)(({ theme: { palette } }) => ({
  border: 'none',
  [`&.${tableCellClasses.head}`]: {
    fontWeight: 'bold',
    color: palette.text.mute
  },
  // [`&.${tableCellClasses.body}`]: {
  //   border: 'none'
  // },
}));

const StyledHeaderRow = styled(TableRow)(({ theme: { palette, spacing } }) => ({
  backgroundColor: palette.background.paper,
  border: `none`
}));

const StyledTableRow = styled(TableRow)(({ theme: { palette, spacing } }) => ({
  backgroundColor: palette.background.paper,
  // border: `solid ${spacing(1)} black`
}));

const PaginationContainer = styled(Box)(({ theme: { palette, spacing } }) => ({
  position: 'sticky',
  bottom: 0,
  width: '100%',
  display: 'flex',
  justifyContent: 'space-between',
  alignItems: 'center',
  border: `solid 1px ${palette.grey[300]}`,
  marginTop: spacing(1),
  padding: spacing(2),
  backgroundColor: palette.background.paper,
  '& > div': {
  }
}))

const StyledPagination = styled(Pagination)(() => ({

}))

const StyledPaginationItem = styled(PaginationItem)(({ theme: { palette, shape } }) => ({
  height: 32,
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  borderRadius: parseInt(`${shape.borderRadius}`) / 2,
  border: `solid 1px ${palette.grey[200]}`
}))

const StyledSortIcon = styled(SyncAlt)(() => ({
  transform: 'rotate(90deg)'
}))

export {
  StyledTable,
  StyledTableBody,
  StyledTableCell,
  StyledHeaderRow,
  StyledTableRow,
  StyledPaper,
  PaginationContainer,
  StyledPagination,
  StyledPaginationItem,
  StyledSortIcon
}